/* global FileReader, Blob, atob, window */
import Promise from 'native-promise-only';
import ObjectId from 'bson-objectid';
import querystring from 'querystring';
import omit from 'lodash/omit';
import forOwn from 'lodash/forOwn';
import zipObject from 'lodash/zipObject';
import cloneDeep from 'lodash/cloneDeep';
import { eachComponent, getValue } from 'formiojs/utils/formUtils';
import PouchDB from 'pouchdb-browser';
import PouchFind from 'pouchdb-find';
import PouchErase from 'pouchdb-erase';

PouchDB.plugin(PouchFind);
PouchDB.plugin(PouchErase);

// Prefixes used with offline cache entries in db
const OFFLINE_PROJECT_PREFIX = 'projectCache';
const OFFLINE_SUBMISSION_PREFIX = 'submissionCache';
const OFFLINE_STATIC_PREFIX = 'staticCache';
const OFFLINE_QUEUE_KEY = 'offlineQueue';
const OFFLINE_FILE_QUEUE = 'fileQueue';
const enableDebug = false;
const debug = function(...args) {
  if (enableDebug) {
    console.log(...args);
  }
};

// Poor-mans deep copy.
const copy = function(obj) {
  return JSON.parse(JSON.stringify(obj));
};

/**
 * A proxy to a deferred class.
 */
class Deferred {
  constructor() {
    this.state = 'pending';
    this.promise = new Promise((resolve, reject) => {
      this.resolve = (...args) => {
        this.state = 'fulfilled';
        resolve(...args);
      };
      this.reject = (...args) => {
        this.state = 'rejected';
        reject(...args);
      };
    });
  }
}

export default class FormioOfflineProject {
  /**
   * @param  url  The project url
   * @param  path Path to project.json with initial forms definition (optional)
   * @return {[type]}      [description]
   */
  constructor(url, path) {
    this.priority = 100;
    this.readyQueue = [];
    this.errorQueue = [];
    this.blobSupport = true;

    if (!url) {
      throw new Error('No project url provided.');
    }

    // Store url and path until we get Formio access in init method
    this.projectUrl = url;
    this.projectJsonPath = path;

    // Initialize key-value based storage in index db.
    this.db = {
      database: '',
      dbs: {},
      dbPromise: null,
      toPouch(doc) {
        if (!doc) {
          return null;
        }
        // Transform from our format to pouch expected. (Can't start with underscores)
        Object.keys(doc).forEach(key => {
          if (!['_id', '_rev', '_deleted'].includes(key) && key.startsWith('_')) {
            doc[`$${key}`] = doc[doc];
            delete doc[key];
          }
        });

        return doc;
      },
      fromPouch(doc) {
        if (!doc) {
          return null;
        }
        // Transform back to our format.
        Object.keys(doc).forEach(key => {
          if (!['_id', '_rev', '_deleted'].includes(key) && key.startsWith('$')) {
            doc[key.substring(1)] = doc[key];
            delete doc[key];
          }
        });

        // Deconstruct _id.
        const parts = doc._id.split('-');
        doc._id = parts[parts.length - 1];

        return doc;
      },
      init(database) {
        this.dbs[OFFLINE_PROJECT_PREFIX] = new PouchDB(database + '-' + OFFLINE_PROJECT_PREFIX);
        this.dbs[OFFLINE_SUBMISSION_PREFIX] = new PouchDB(database + '-' + OFFLINE_SUBMISSION_PREFIX);
        this.dbs[OFFLINE_STATIC_PREFIX] = new PouchDB(database + '-' + OFFLINE_STATIC_PREFIX);
        this.dbs[OFFLINE_QUEUE_KEY] = new PouchDB(database + '-' + OFFLINE_QUEUE_KEY);
        this.dbs[OFFLINE_FILE_QUEUE] = new PouchDB(database + '-' + OFFLINE_FILE_QUEUE);
        this.database = database;
        return Promise.resolve(this);
      },
      getItem(storage, key) {
        return this.dbs[storage].get(key)
          .then(this.fromPouch)
          .catch(err => {
            // If not found, return null.
            return null;
          });
      },
      setItem(storage, key, val) {
        // Do not store anything that does not have a key or a value.
        if (!key || !val) {
          return Promise.resolve(null);
        }
        val._id = key;
        val = this.toPouch(val);
        return this.dbs[storage].get(key)
          .then(doc => {
            // If it does exist, update the _rev.
            val._rev = doc._rev;
            return val;
          })
          .catch((err) => {
            // If existing doesn't exist, just return the val.
            delete val._rev;
            return val;
          })
          .then(doc => {
            this.dbs[storage].put(doc);
          })
          .then(() => {
            return this.dbs[storage].get(key);
          });
      },
      setItems(storage, items, prefix = '') {
        return this.dbs[storage].allDocs()
          .then(existing => {
            // We need to know the latest _rev for each item already in the database or else we can't bulk write.
            const revMap = existing.rows.reduce((prev, item) => {
              prev[item._id] = item._rev;
              return prev;
            }, {});

            // Fix each item for pouch and add _rev if it exists.
            items = items.map(item => {
              // Append id prefix
              item._id = prefix + item._id;

              item = this.toPouch(item);

              if (revMap.hasOwnProperty(item._id)) {
                item._rev = revMap[item._id];
              }
              else {
                delete item._rev;
              }

              return item;
            });

            return this.dbs[storage].bulkDocs(items);
          });
      },
      removeItem(storage, key) {
        return this.dbs[storage].get(key).then((doc) => {
          return this.dbs[storage].remove(doc);
        });
      },
      search(storage, formId, query) {
        const getFindQuery = function(query) {
          const findQuery = {};

          // Get the filters and omit the limit, skip, select, sort and populate.
          const filters = omit(query, 'limit', 'skip', 'select', 'sort', 'populate');

          // Iterate through each filter.
          forOwn(filters, (value, name) => {
            // Get the filter object.
            const filter = zipObject(['name', 'selector'], name.split('__'));

            // See if there is a selector.
            if (filter.selector) {
              // See if this selector is a regular expression.
              if (filter.selector === 'regex') {
                // Set the regular expression for the filter.
                const parts = value.match(/\/?([^/]+)\/?([^/]+)?/);
                let regex = null;
                try {
                  regex = new RegExp(parts[1], (parts[2] || 'i'));
                }
                catch (err) {
                  regex = null;
                }
                if (regex) {
                  findQuery[filter.name] = regex;
                }
                return;
              }
              else {
                // Init the filter.
                if (!findQuery.hasOwnProperty(filter.name)) {
                  findQuery[filter.name] = {};
                }

                if (filter.selector === 'exists') {
                  value = ((value === 'true') || (value === '1')) ? true : value;
                  value = ((value === 'false') || (value === '0')) ? false : value;
                  value = !!value;
                }
                // Special case for in filter with multiple values.
                else if (['in', 'nin'].includes(filter.selector)) {
                  value = Array.isArray(value) ? value : value.split(',');
                }

                findQuery[filter.name][`$${filter.selector}`] = value;
                return;
              }
            }

            // Set the find query to this value.
            findQuery[filter.name] = value;
          });

          // Return the findQuery.
          return findQuery;
        };

        const q = {
          selector: getFindQuery(query)
        };

        // Fill in special keys.
        ['skip', 'limit', 'sort'].forEach(key => {
          if (query.hasOwnProperty(key)) {
            q[key] = query[key];
          }
        });

        return this.dbs[storage].find(q)
          .then((result) => {
            return result.docs.map(this.fromPouch);
          });
      },
      clear(storage) {
        return this.dbs[storage].erase();
      },

    };

    /**
     * Encode the file, based on BLOB support.
     *
     * @param file
     * @returns {*}
     */
    this.encodeFile = function(file) {
      if (this.blobSupport) {
        return Promise.resolve(file);
      }
      return this.fileToDataUri(file);
    };

    this.fileToDataUri = function(file) {
      return new Promise((resolve, reject) => {
        let fileReader = new FileReader();

        fileReader.onload = function() {
          resolve(fileReader.result);
        };

        fileReader.onerror = function(error) {
          reject(error);
        };

        fileReader.readAsDataURL(file);
      });
    };

    // FOR-298 - Backwards compatibility.
    this.ifEncodeFile = this.encodeFile;

    /**
     * Decode the file, based on BLOB support.
     *
     * @param file
     * @returns {*}
     */
    this.decodeFile = function(dataURI) {
      if (this.blobSupport) {
        return Promise.resolve(dataURI);
      }

      return new Promise((resolve, reject) => {
        try {
          // convert base64/URLEncoded data component to raw binary data held in a string
          let byteString;
          if (dataURI.split(',')[0].indexOf('base64') >= 0) {
            byteString = atob(dataURI.split(',')[1]);
          }
          else {
            byteString = unescape(dataURI.split(',')[1]);
          }

          // separate out the mime component
          let mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];

          // write the bytes of the string to a typed array
          let ia = new Uint8Array(byteString.length);
          for (var i = 0; i < byteString.length; i++) {
            ia[i] = byteString.charCodeAt(i);
          }
          resolve(new Blob([ia], {type:mimeString}));
        }
        catch (err) {
          reject(err);
        }
      });
    };

    // FOR-298 - Backwards compatibility.
    this.ifDecodeFile = this.decodeFile;
  }

  statusIndicator(element, passive) {
    let currentCount = 0;
    let currentlyOnline = null;
    const checkStatus = (function() {
      this.ready.then(() => {
        const online = navigator.onLine;
        const count = this.submissionQueueLength();
        if (online !== currentlyOnline || count !== currentCount) {
          while (element.firstChild) {
            element.removeChild(element.firstChild);
          }
          if (!count) {
            const icon = document.createElement('a');
            icon.setAttribute('data-toggle', 'tooltip');
            icon.setAttribute('data-placement', 'bottom');
            icon.setAttribute('title', navigator.onLine ? 'Online' : 'Offline');
            icon.setAttribute('alt', `${navigator.onLine ? 'Online' : 'Offline'}`);
            icon.setAttribute('class', `fa fa-signal glyphicon glyphicon-signal ${navigator.onLine ? 'text-success' : 'text-warning'}`);
            element.appendChild(icon);
          }
          else {
            const indicator = document.createElement('span');
            indicator.setAttribute('class', 'badge badge-pill badge-warning');
            if (count) {
              indicator.appendChild(document.createTextNode(`${count} offline tasks`));
            }
            element.appendChild(indicator);
          }
          currentCount = count;
          currentlyOnline = online;
        }
        setTimeout(() => checkStatus(), 1000);
      });
    }).bind(this);
    this.Formio.events.on('offline.saveQueue', () => checkStatus());
    this.Formio.events.on('offline.queue', () => checkStatus());
    this.Formio.events.on('offline.queueEmpty', () => checkStatus);
    if (!passive) {
      element.addEventListener('click', () => this.dequeueSubmissions());
    }
    checkStatus();
  }

  /**
   * Determine if this plugin is ready for use.
   *
   * @returns {Promise}
   *   A promise to resolve when the offline mode plugin is ready for external use.
   */
  get ready() {
    return Promise.all(this.readyQueue);
  }

  /******************* Plugin Functions *******************/

  /**
   * Initialize the offline mode plugin. Called externally by Formiojs#registerPlugin();
   *
   * @param {Formio} Formio
   *   An instance of Formiojs.
   */
  init(Formio) {
    this.Formio = Formio;
    this.projectId = new Formio(this.projectUrl).projectId || 'formio';
    this.offlineProject = {forms: {}};
    this.submissionQueue = [];
    this.dequeuing = false;
    this.forcedOffline = false;
    this.offline = false;
    this.cacheError = false;
    this.debugBroadcast = function(err) {
      debug(this.event);
      debug(err);
    };

    // Log all the events if debug is enabled.
    this.Formio.events.on('offline.saveQueue', this.debugBroadcast);
    this.Formio.events.on('offline.queue', this.debugBroadcast);
    this.Formio.events.on('offline.queueEmpty', this.debugBroadcast);
    this.Formio.events.on('offline.dequeue', this.debugBroadcast);
    this.Formio.events.on('offline.formError', this.debugBroadcast);
    this.Formio.events.on('offline.formSubmission', this.debugBroadcast);
    this.Formio.events.on('offline.requeue', this.debugBroadcast);

    // Override this to catch errors with cache when offline.
    this.onError = function(err) {
      console.error('Error: ', err);
    };

    // Check for offline capabilities; warn the user if indexedDB is not available.
    if (!window.indexedDB) {
      this.enabled = false;
      console.error('Form.io Offline Plugin not available, because indexedDB is not available in this Browser.');
      return;
    }
    this.enabled = true;

    this.dbPromise = this.db.init('formio' + this.projectId);

    this.readyQueue.push(this.dbPromise);

    // Load offline project
    this.readyQueue.push(
      this.getItem(OFFLINE_PROJECT_PREFIX, this.projectId)
      .then((cached) => {
        if (cached) {
          return cached;
        }

        // Return an empty project so requests start caching offline.
        if (!this.projectJsonPath) {
          return {forms: {}};
        }

        // Otherwise grab offline project definition
        return fetch(this.projectJsonPath)
        .then((response) => response.json())
        .then((project) => {
          project.forms = project.forms || {};

          // Move resources into forms collection, easier to manage that way
          Object.keys(project.resources || {}).forEach((resourceName) => {
            project.forms[resourceName] = project.resources[resourceName];
          });
          delete project.resources;

          // Set modified time as early as possible so any newer
          // form will override this one if there's a name conflict.
          Object.keys(project.forms).forEach((formName) => {
            project.forms[formName].created = new Date(0).toISOString();
            project.forms[formName].modified = new Date(0).toISOString();
            project.forms[formName].offline = true;
          });
          return project;
        });
      })
      .then((project) => {
        this.offlineProject = project;
        return this.setItem(OFFLINE_PROJECT_PREFIX, this.projectId, project);
      })
      .catch((err) => {
        // Swallow the error so failing caching doesn't halt the ready promise chain
        console.error('Error caching offline storage: ', err);
      })
    );

    // Load offline submission queue
    this.readyQueue.push(
      this.getItem(OFFLINE_QUEUE_KEY, 'queue')
      .then((result) => {
        result = result || {};
        this.submissionQueue = result.queue || [];
      })
      .catch((err) => {
        console.error('Error restoring offline submission queue: ', err);
      })
    );

    // Wrap functions to bind to this.
    this.forceOffline = this.forceOffline.bind(this);
    this.isForcedOffline = this.isForcedOffline.bind(this);
    this.clearOfflineData = this.clearOfflineData.bind(this);
    this.dequeueSubmissions = this.dequeueSubmissions.bind(this);
    this.submissionQueueLength = this.submissionQueueLength.bind(this);
    this.getNextQueuedSubmission = this.getNextQueuedSubmission.bind(this);
    this.setNextQueuedSubmission = this.setNextQueuedSubmission.bind(this);
    this.skipNextQueuedSubmission = this.skipNextQueuedSubmission.bind(this);
    this.preRequest = this.preRequest.bind(this);
    this.request = this.request.bind(this);
    this.staticRequest = this.staticRequest.bind(this);
    this.fileRequest = this.fileRequest.bind(this);
    this.wrapRequestPromise = this.wrapRequestPromise.bind(this);
    this.wrapStaticRequestPromise = this.wrapStaticRequestPromise.bind(this);
    this.wrapFileRequestPromise = this.wrapFileRequestPromise.bind(this);
    this.sendRequest = this.sendRequest.bind(this);
    this.setItem = this.setItem.bind(this);
    this.setItems = this.setItems.bind(this);
    this.getItem = this.getItem.bind(this);
  }

  preRequest() {
    return this.ready;
  }

  request({formio, type, url, method, data, opts}) {
    debug(`request - [${method}] <${type}> : ${url}`);
    opts = opts || {};

    if (this.enabled && method === 'GET') {
      opts.ignoreCache = true;
    }

    // If queuing is enabled, requests need to be queued regardless of if we're online or offline.
    if (opts.skipQueue || type !== 'submission' || !['POST', 'PUT', 'DELETE'].includes(method)) {
      if (this.isForcedOffline()) {
        return this._throwOfflineError();
      }

      return;
    }

    // Push request to end of offline queue
    let queuedRequest = {
      request: {
        _id: formio.submissionId || data._id || ObjectId.generate(),
        type,
        url,
        method,
        data: null,
        created: new Date().toISOString(),
        form: formio.formId,
        formUrl: formio.formUrl
      }
    };

    if (data) {
      // Perform deep copy.
      const payload = copy({
        data: data.data,
        metadata: data.metadata,
        state: data.state
      });
      let user = this.Formio.getUser();
      queuedRequest.request.data = {
        _id: queuedRequest.request._id,
        owner: user ? user._id : null,
        form: formio.formId,
        data: payload.data,
        metadata: payload.metadata,
        state: payload.state,
        created: queuedRequest.request.created,
        modified: queuedRequest.request.created,
        externalIds: [],
        roles: []
      };
    }

    // Set enumerable to false, IndexedDB doesn't like serializing
    // Q Deferred objects.
    Object.defineProperty(queuedRequest, 'deferred', {
      enumerable: false,
      value: new Deferred()
    });

    this.submissionQueue.push(queuedRequest);

    // Save the queue.
    return this._saveQueue()
      .then(() => {
        this.Formio.events.emit('offline.queue', queuedRequest.request);
        this.dequeueSubmissions();
        return queuedRequest.deferred.promise;
      })
      .catch((err) => console.warn(err));
  }

  staticRequest() {
    if (!this.isForcedOffline()) {
      return;
    }

    // Go to offline logic.
    return Promise.resolve()
    .then(() => this._throwOfflineError());
  }

  fileRequest(request) {
    if (this.forcedOffline) {
      // Fake a network error so we go straight into offline logic
      let err = new Error('Formio is forced into offline mode.');
      err.networkError = true;
      return Promise.reject(err);
    }

    if (request.method === 'download' && request.file.storage === 'offline') {
      return this.db.getItem(OFFLINE_FILE_QUEUE, request.file.name)
      .then((result) => {
        if (result) {
          return this.encodeFile(result.file);
        }
        else {
          return Promise.resolve();
        }
      })
      .then((response) => {
        if (!(response instanceof Blob)) {
          request.file.url = response;
        }
        return request.file;
      });
    }

    // If we get this far, pass back nothing so default action runs.
    return Promise.resolve();
  }

  fromIdbToUri(fileName) {
    return this.db.getItem(OFFLINE_FILE_QUEUE, fileName).then(response => {
      if (response && response.file) {
        return this.fileToDataUri(response.file);
      }
      else {
        return Promise.resolve(fileName);
      }
    });
  }

  wrapRequestPromise(promise, {formio, type, url, method}) {
    debug('wrapRequestPromise', `[${method}] <${type}> : ${url}`);
    //if (formio.projectId !== this.projectId) {
    //  debug('Project Ids don\'t match', formio.projectId, this.projectId);
    //  return promise;
    //}

    return promise
    .then((result) => {
      debug('request result', result);
      if (result.offline) {
        this.offline = true;
        return result;
      }

      this.offline = false;
      return result;
    })
    .catch((err) => {
      debug('request err', err);
      // A regular error, no offline logic needed
      if (!err.networkError) {
        throw err;
      }

      // If offline mode is not enabled, return the offline error.
      if (!this.enabled) {
        throw err;
      }

      // If this is a network error and offline is enabled, set as in offline mode.
      this.offline = true;

      // Try to get offline cached response if offline
      let cache = this.offlineProject;

      // Form GET
      if (type === 'form' && method === 'GET') {
        if (!cache || !cache.forms) throw err;
        // Find and return form
        let form = Object.keys(cache.forms).reduce((result, name) => {
          if (result) {
            return result;
          }

          let form = cache.forms[name];
          if (form._id === formio.formId || form.path === formio.formId) {
            return form;
          }
        }, null);

        if (!form) {
          err.message += ' (No offline cached data found)';
          throw err;
        }

        return form;
      }

      // Form INDEX
      if (type === 'forms' && method === 'GET') {
        if (!cache || !cache.forms) {
          throw err;
        }

        // Response should be an array, not an object.
        var forms = [];
        for (var key in cache.forms) {
          forms.push(cache.forms[key]);
        }
        let qs = querystring.parse(url.split('?')[1] || '');
        if (qs.type) {
          forms = forms.filter(function(form) {
            return form.type === qs.type;
          });
        }
        if (qs.tags) {
          var tags = qs.tags.split(',');
          forms = forms.filter(function(form) {
            for (var i = 0; i < tags.length; i++) {
              if (form.tags && form.tags.indexOf(tags[i]) !== -1) {
                return true;
              }
            }
            return false;
          });
        }
        return forms;
      }

      // Submission GET
      if (type === 'submission' && method === 'GET') {
        return this._getOfflineSubmission(formio.formId, formio.submissionId)
        .then((submission) => {
          if (!submission) {
            throw err;
          }

          return submission;
        });
      }

      // Submission INDEX
      if (type === 'submissions' && method === 'GET') {
        let qs = querystring.parse(url.split('?')[1] || '');

        return this.db.search(OFFLINE_SUBMISSION_PREFIX, formio.formId, qs)
        .then((submissions) => {
          let limit = Number(qs.limit) || 10;
          let skip = Number(qs.skip) || 0;
          submissions.limit = limit;
          submissions.skip = skip;
          // We don't have a way to get a count of available submissions, so say there is one more page.
          submissions.serverCount = limit + skip + 1;

          return submissions;
        });
      }

      throw err; // No offline logic, just throw the error
    })
    .then((result) => {
      // Check if need to update cache after request
      var cache = this.offlineProject;
      if (!cache) {
        debug('Is offline, don\'t update cache');
        return result;
      }

      if (type === 'form' && method !== 'DELETE' && !result.offline) {
        if (cache.forms[result.name] && new Date(cache.forms[result.name].modified) >= new Date(result.modified)) {
          // Cache is up to date
          return result;
        }
        var cachedResult = copy(result);
        cachedResult.offline = true;
        cache.forms[result.name] = cachedResult;
      }
      else if (type === 'form' && method === 'DELETE') {
        let {_id, path} = this._resolveFormId(formio.formId);
        var formPath = '';
        cache.forms.forEach(function(form) {
          if (form.path === path || form._id === _id) {
            formPath = form.path;
          }
        });
        delete cache.forms[formPath];
      }
      else if (type === 'forms' && method === 'GET') {
        if (result.length && result[0].offline) return result;

        // Don't replace all forms, as some may be omitted due to permissions
        result.forEach(function(form) {
          var cachedForm = copy(form);
          cachedForm.offline = true;
          cache.forms[form.name] = cachedForm;
        });
      }
      else if (type === 'submission' && method !== 'DELETE' && !result.offline) {
        this._saveOfflineSubmission(formio.formId, result).catch(() => {});
        return result;
      }
      else if (type === 'submission' && method === 'DELETE' && !result.offline) {
        this._deleteOfflineSubmission(formio.formId, formio.submissionId).catch(() => {});
        return result;
      }
      else if (type === 'submissions' && method === 'GET') {
        // Skip caching if there are no results or if it already came from offline.
        if (!result.length || result[0].offline) {
          return result;
        }

        try {
          this.setItems(OFFLINE_SUBMISSION_PREFIX, result, formio.formId + '-');
        }
        catch (err) {
          console.warn('Error storing offline submissions:', err);
        }

        return result;
      }
      else {
        // Nothing to cache
        return result;
      }

      // Update db
      this._removeCacheDuplicates(cache); // Clean up duplicates
      try {
        this.readyQueue.push(
          this.setItem(OFFLINE_PROJECT_PREFIX, formio.projectId, cache)
        );
      }
      catch (err) {
        console.warn('Error updating project cache:', err);
      }

      return result;
    });
  }

  wrapStaticRequestPromise(promise, {url, method}) {
    debug('wrapStaticRequestPromise', `[${method}] : ${url}`);
    return promise
    .then((result) => {
      debug('static result', result);
      this.offline = false;
      return result;
    })
    .catch((err) => {
      debug('static err', err);
      // A regular error, no offline logic needed
      if (!err.networkError) {
        throw err;
      }

      // If offline mode is not enabled, return the offline error.
      if (!this.enabled) {
        throw err;
      }

      return this.getItem(OFFLINE_STATIC_PREFIX, method + '-' + url)
      .then((result) => {
        if (!result) {
          throw err;
        }

        return result;
      });
    })
    .then((result) => {
      try {
        this.setItem(OFFLINE_STATIC_PREFIX, method + '-' + url, result);
      }
      catch (err) {
        // Swallow the error
        console.warn('Error caching static request:', err);
      }

      return result;
    });
  }

  wrapFileRequestPromise(promise, request) {
    debug('wrapFileRequestPromise', `[${request.method}] <${request.type}> : ${request.url}`);
    return promise
    .then((result) => {
      debug('file result');
      this.offline = false;
      return result;
    })
    .catch((err) => {
      debug('file error');
      // A regular error, no offline logic needed
      if (!err.networkError) {
        throw err;
      }

      // If offline mode is not enabled, return the offline error.
      if (!this.enabled) {
        throw err;
      }

      if (request.method === 'upload') {
        // Async save the file for later.
        return this.encodeFile(request.file)
          .then((file) => {
            request.file = file;

            return this.db.setItem(OFFLINE_FILE_QUEUE, request.fileName, request)
            .then(() => {
              // Return a temporary item.
              return {
                storage: 'offline',
                name: request.fileName,
                size: request.file.size,
                type: request.file.type
              };
            });
          });
      }

      // TODO: If we were caching files offline, we would return it here.
      // If we don't know what else to do. Throw the original error.
      throw err;
    });
  }

  /******************* Public Functions *******************/

  /**
   * Set the forcedOffline property, used for simulating no network access.
   *
   * @param {Boolean} offline
   *   The desired offline state to set.
   */
  forceOffline(offline) {
    this.forcedOffline = (offline === true);
  }

  /**
   * Determine if the plugin is being forced to simulate offline access or not.
   *
   * @returns {Boolean}
   *   Whether or not the plugin is in forced offline mode.
   */
  isForcedOffline() {
    return (this.forcedOffline === true);
  }

  /**
   * Returns the index of the item in the submission queue.
   *
   * @param submission
   * @return {number}
   */
  getSubmissionQueueIndex(submission) {
    let index = -1;
    if (submission._id && submission.form) {
      for (let i = 0; i < this.submissionQueue.length; i++) {
        if (
          this.submissionQueue[i].request &&
          this.submissionQueue[i].request.form === submission.form &&
          this.submissionQueue[i].request._id === submission._id
        ) {
          index = i;
          break;
        }
      }
    }
    return index;
  }

  /**
   * Get the length of the current submission queue.
   *
   * @returns {Number}
   *   The length of the submission queue.
   */
  submissionQueueLength() {
    return (this.submissionQueue || []).length;
  }

  /**
   * Returns an item in the submission queue.
   *
   * @param form
   * @param _id
   * @return {null}
   */
  getSubmissionQueueSubmission(form, _id) {
    const index = this.getSubmissionQueueIndex({form, _id});
    return (index === -1) ? null : copy(this.submissionQueue[index]);
  }

  /**
   * Empty the submission queue.
   *
   * @return {Promise}
   */
  emptySubmissionQueue() {
    this.submissionQueue = [];
    return this.ready
      .then(() => this._saveQueue())
      .then(() => this.db.clear(OFFLINE_QUEUE_KEY));
  }

  /**
   * Removes an item in the submission queue.
   *
   * @param submission
   * @return {*}
   */
  removeSubmissionQueueSubmission(submission) {
    const index = this.getSubmissionQueueIndex(submission);
    if (index !== -1) {
      this.submissionQueue.splice(index, 1);
      return this._saveQueue();
    }
    return Promise.resolve();
  }

  /**
   * Updates an item in the submission queue.
   *
   * @param submission
   * @return {*}
   */
  updateSubmissionQueueSubmission(submission) {
    const index = this.getSubmissionQueueIndex(submission);
    if (index !== -1) {
      this._saveOfflineSubmission(this.submissionQueue[index].request.form, submission);
      this.submissionQueue[index].request.data = copy(submission);
      return this._saveQueue();
    }
    return Promise.resolve();
  }

  /**
   * Get the next request in the submission queue.
   *
   * @returns {Object}
   *   The first request in the queue, or undefined if the current queue is empty.
   */
  getNextQueuedSubmission() {
    if (this.submissionQueueLength() === 0) {
      return undefined;
    }

    return this.submissionQueue[0].request;
  }

  /**
   * Modify the first request in the submission queue and persist the queue changes.
   *
   * @param {Object} request
   *   The request to persist for the first element in the submission queue.
   *
   * @returns {Promise}
   *   A promise for when the operation is complete.
   */
  setNextQueuedSubmission(request) {
    if (this.submissionQueueLength() === 0) {
      return Promise.reject('The submissionQueue is empty.');
    }

    this.submissionQueue[0].request = request;
    return this._saveQueue();
  }

  /**
   * Skip the next request in the queue and persist the queue changes.
   *
   * @returns {Promise}
   *   A promise for when the operation is complete.
   */
  skipNextQueuedSubmission() {
    this.submissionQueue.shift();
    return this._saveQueue();
  }

  clearOfflineSubmissions() {
    return this.ready.then(() => this.db.clear(OFFLINE_SUBMISSION_PREFIX));
  }

  /**
   * Clear all offline mode data.
   *
   * @returns {Promise}
   *   Thenable once db has been cleared.
   */
  clearOfflineData() {
    // Clear in-memory cache
    this.offlineProject = {forms: {}};
    this.submissionQueue = [];
    return this.ready
      .then(() => this.db.clear(OFFLINE_QUEUE_KEY))
      .then(() => this.db.clear(OFFLINE_PROJECT_PREFIX))
      .then(() => this.db.clear(OFFLINE_SUBMISSION_PREFIX))
      .then(() => this.db.clear(OFFLINE_STATIC_PREFIX));
  }

  sendRequest(request) {
    return this.Formio.request(request.url, request.method, request.data);
  }

  dequeueSubmissions(skipError = false) {
    let processRequest = (submission) => {
      // Remove request from queue
      let queuedRequest = this.submissionQueue.shift();
      this._saveQueue();

      // If using a temporary id, update subsequent requests with the correct id.
      if (queuedRequest.request.method === 'POST' && submission._id !== queuedRequest.request.data._id) {
        let oldId = queuedRequest.request.data._id;
        this.submissionQueue.forEach((nextRequest) => {
          if (
            (nextRequest.request._id || nextRequest.request.data._id) === oldId &&
            ['PUT', 'DELETE'].includes(nextRequest.request.method)
          )  {
            nextRequest.request._id = submission._id;
            if (nextRequest.request.data) {
              nextRequest.request.data._id = submission._id;
            }
            nextRequest.request.url = nextRequest.request.url.replace(oldId, submission._id);
            this._saveQueue();
          }
        });
      }

      // Update the queue items by deleting old one first, then add the new submission. Fixes problems with temp id
      // creates getting duplicates.
      this._deleteOfflineSubmission(queuedRequest.request.form, queuedRequest.request._id)
        .then(() => {
          if (queuedRequest.request.method !== 'DELETE') {
            return this._saveOfflineSubmission(queuedRequest.request.form, submission).catch(() => {});
          }
        })
        .catch(() => {});

      // Resolve promise if it hasn't already been resolved with a fake value
      if (queuedRequest.deferred && queuedRequest.deferred.state === 'pending') {
        queuedRequest.deferred.resolve(submission);
      }
      else if (queuedRequest.request.method !== 'DELETE') {
        // Emit submission if promise isn't available to reject
        this.Formio.events.emit('offline.formSubmission', submission, queuedRequest);
      }

      // Continue to next queue item
      this.dequeuing = false;
      this.dequeueSubmissions(skipError);
    };

    let catchRequest = (err) => {
      // Stop dequeuing because we got a network error trying to request
      this.dequeuing = null;
      let queuedRequest = this.submissionQueue[0];

      if (!err.networkError) {
        this.Formio.events.emit('offline.formError', err, queuedRequest);

        if (skipError) {
          this.errorQueue.push(this.submissionQueue.shift());
          this.dequeueSubmissions(skipError);
        }

        if (queuedRequest.deferred && queuedRequest.deferred.state === 'pending') {
          // If we can reject the promise, no need to keep it in the queue
          this.submissionQueue.shift();
          this._saveQueue();
          queuedRequest.deferred.reject(err);
        }
      }

      // Emit an event indicating that the request will be retried later
      // if it hasn't already been removed from the queue
      if (queuedRequest === this.submissionQueue[0]) {
        this.Formio.events.emit('offline.requeue', queuedRequest);
      }

      // Go through all queued requests and resolve with fake data so
      // app can continue offline
      const pendingRequests = this.submissionQueue.filter((queuedRequest) =>
        queuedRequest.deferred && queuedRequest.deferred.state === 'pending');

      pendingRequests
        .filter((queuedRequest) => queuedRequest.request.method === 'POST')
        .forEach((queuedRequest) => {
          let user = this.Formio.getUser();
          Object.assign(queuedRequest.request.data, {
            offline: true,
            owner: user ? user._id : null,
            created: new Date().toISOString(),
            modified: new Date().toISOString(),
          });
          this._saveOfflineSubmission(queuedRequest.request.form, queuedRequest.request.data).catch(() => {});
          queuedRequest.deferred.resolve(queuedRequest.request.data);
        });

      pendingRequests
        .filter((queuedRequest) => queuedRequest.request.method === 'PUT')
        .forEach((queuedRequest) => {
          this._getOfflineSubmission(queuedRequest.request.form, queuedRequest.request._id, queuedRequest)
            .then((submission) => {
              submission.data = queuedRequest.request.data.data;
              submission.modified = new Date().toISOString();
              queuedRequest.request.data = submission;
              this._saveOfflineSubmission(submission.form, submission).catch(() => {});
              queuedRequest.deferred.resolve(submission);
            }).catch(() => {});
        });

      pendingRequests
        .filter((queuedRequest) => queuedRequest.request.method === 'DELETE')
        .forEach((queuedRequest) => {
          queuedRequest.deferred.resolve({offline: true});
        });

      this._saveQueue();

      // If cache threw an error and we got a network error, we're in a really bad state.
      if (this.cacheError) {
        this.onError(this.cacheError);
      }
    };

    this.ready.then(() => {
      if (this.dequeuing) {
        return;
      }

      // If the queue is empty, delete any extra files as well.
      if (!this.submissionQueue.length) {
        if (skipError) {
          this.submissionQueue.push(...this.errorQueue);
          this._saveQueue();
          this.errorQueue = [];
        }

        return;
      }

      let {request} = this.submissionQueue[0];
      this.dequeuing = true;
      this.Formio.events.emit('offline.dequeue', request);

      return Promise.resolve()
      .then(() => {
        // Fake a network error so we go straight into offline logic
        if (this.isForcedOffline()) {
          return this._throwOfflineError();
        }

        if (!['PUT', 'POST'].includes(request.method)) {
          return;
        }
        return this._resolveFiles(request.form, request.formUrl, request.data);
      })
      .then(() => this.sendRequest(request))
      .then(processRequest)
      .catch(catchRequest);
    });
  }

  /**
   * Public wrapper around db.setItem
   *
   * @param storage
   * @param key
   * @param value
   * @returns {*}
   */
  setItem(storage, key, value) {
    if (!this.enabled) {
      return Promise.resolve();
    }

    try {
      return this.db.setItem(storage, key, value);
    }
    catch (err) {
      this.cacheError = err;
      throw err;
    }
  }

  /**
   * Public wrapper around db.setItems
   *
   * @param storage
   * @param key
   * @param value
   * @returns {*}
   */
  setItems(storage, items, prefix) {
    if (!this.enabled) {
      return Promise.resolve();
    }

    try {
      return this.db.setItems(storage, items, prefix);
    }
    catch (err) {
      this.cacheError = err;
      throw err;
    }
  }

  /**
   * Public wrapper around db.getItem
   *
   * @param storage
   * @param key
   * @returns {*}
   */
  getItem(storage, key) {
    return this.db.getItem(storage, key);
  }

  /******************* Internal Functions *******************/

  _throwOfflineError() {
    let err = new Error('Formio is forced into offline mode.');
    err.networkError = true;
    throw err;
  }

  /**
   * Saves the submissionQueue to db
   * @return Promise for the save operation.
   */
  _saveQueue() {
    if (!this.submissionQueue.length) {
      this.Formio.events.emit('offline.queueEmpty');
    }
    const savePromise = this.setItem(OFFLINE_QUEUE_KEY, 'queue', { queue: this.submissionQueue })
      .then(() => {
        this.Formio.events.emit('offline.saveQueue', this.submissionQueue.length);
      })
      .catch((err) => {
        // Swallow error so it doesn't halt the ready chain
        console.warn('Error persisting submission queue:', err);
        return Promise.resolve();
      });
    this.readyQueue.push(savePromise);
    return savePromise;
  }

  /**
   * Removes duplicate forms from offline cached project.
   * Duplicates can occur if form is renamed (old and new
   * stored under different names but have same id/path).
   * NOTE: modifies the given object
   *
   * @param projectCache Project cache object
   */
  _removeCacheDuplicates(projectCache) {
    Object.keys(projectCache.forms).forEach((name) => {
      let form = projectCache.forms[name];
      if (!form) { // form was deleted
        return;
      }
      Object.keys(projectCache.forms).forEach((otherName) => {
        let otherForm = projectCache.forms[otherName];
        if ((form._id === otherForm._id || form.path === otherForm.path) &&
          new Date(otherForm.modified) < new Date(form.modified)) {
          delete projectCache.forms[otherName];
        }
      });
    });
  }

  /**
   * @param formPath Path to form
   * @return true if form is part of a project that has been cached offline
   */
  _isFormOffline(formPath) {
    if (this.offlineProject.forms[formPath]) {
      return true;
    }
    for (let formKey of Object.keys(this.offlineProject.forms)) {
      if (this.offlineProject.forms[formKey].path === formPath || this.offlineProject.forms[formKey]._id === formPath) {
        return true;
      }
    }
    return false;
  }

  /**
   * Returns an object with form _id and path
   * @param  formId The form's _id or path
   * @return An object with both the formId and formPath
   */
  _resolveFormId(formId) {
    // formId is the form's _id
    let result = {
      _id: null,
      path: null
    };
    if (ObjectId.isValid(formId)) {
      result._id = formId;
      Object.keys(this.offlineProject.forms).forEach((formPath) => {
        let form = this.offlineProject.forms[formPath];
        if (form._id === formId) result.path = formPath;
      });
    }
    // formId is the form's path
    else {
      result.path = formId;
      let form = this.offlineProject.forms[formId];
      if (form) result._id = form._id;
    }

    return result;
  }

  /**
   * Find a form in the offline project by path
   */
  _getFormByPath(pathOrId) {
    for (var key in this.offlineProject.forms) {
      if (this.offlineProject.forms.hasOwnProperty(key)) {
        if (this.offlineProject.forms[key].path === pathOrId || this.offlineProject.forms[key]._id === pathOrId) {
          return cloneDeep(this.offlineProject.forms[key]);
        }
      }
    }
  }

  /**
   * Finds all files stored in offline mode, syncs them up and replaces the files with online versions.
   * @param form The form name
   * @param form The form url
   * @param submission The submission object
   * @return Promise when complete
   */
  _resolveFiles(formPath, formUrl, submission) {
    let fileQueue = [];
    if (!submission) {
      return Promise.resolve();
    }

    const form = this._getFormByPath(formPath);

    if (form === undefined) {
      return Promise.reject('No form found');
    }

    form.components = form.components || [];
    eachComponent(form.components, (component) => {
      if (component.type !== 'file') {
        return;
      }

      let value = getValue(submission, component.key);
      if (value === null || value === undefined || !(value instanceof Array)) {
        return;
      }

      value.forEach((item, index) => {
        if (item.storage !== 'offline') {
          return;
        }

        fileQueue.push(
          this.db.getItem(OFFLINE_FILE_QUEUE, item.name)
          .then((request) => {
            return this.decodeFile(request.file)
            .then((file) => {
              let formio = new this.Formio(formUrl);
              return formio.uploadFile(request.provider, file, request.fileName, request.dir);
            });
          })
          .then((result) => {
            if (result.storage === 'offline') {
              let err = new Error('Could not resolve online file.');
              err.networkError = true;
              throw err;
            }

            // Replace the offline file object with the online file object.
            value[index] = result;

            // Remove the file as it has now been uploaded.
            return this.db.removeItem(OFFLINE_FILE_QUEUE, item.name);
          })
        );
      });
    });

    return Promise.all(fileQueue);
  }

  /**
   * Save a submission to retrieve in GET requests while offline.
   * @param  formId     ID or path of submission's form
   * @param  submission Submission to save
   * @return Promise for the save operation
   */
  _saveOfflineSubmission(formId, submission) {
    let {_id, path} = this._resolveFormId(formId);
    if (!this._isFormOffline(path)) {
      debug('Form is not offline', formId, path);
      return Promise.resolve();
    }
    formId = path || _id;
    let offlineSubmission = copy(submission);
    offlineSubmission.offline = true;
    return this.setItem(OFFLINE_SUBMISSION_PREFIX, formId + '-' + submission._id, offlineSubmission)
    .catch((err) => {
      console.warn('Failed to save offline submission:', err);
      throw err;
    });
  }

  /**
   * Attempts to find and return an offline cached submission.
   * @param  formId              The _id or path of the submission's form
   * @param  submissionId        The _id of the submission
   * @param  ignoreQueuedRequest Optional queuedRequest that you want to ignore
   * @return                     Submission if available offline, otherwise null
   */
  _getOfflineSubmission(formId, submissionId, ignoreQueuedRequest) {
    let {_id, path} = this._resolveFormId(formId);
    // Try to load offline cached submission
    return this.getItem(OFFLINE_SUBMISSION_PREFIX, `${path}-${submissionId}`)
    .then((submission) => submission || this.getItem(OFFLINE_SUBMISSION_PREFIX, `${_id}-${submissionId}`))
    .then((submission) => submission || this.submissionQueue.reduce((result, queuedRequest) => {
      if (
        (queuedRequest.request._id || queuedRequest.request.data._id) !== submissionId ||
        queuedRequest === ignoreQueuedRequest
      ) {
        return result;
      }

      if (queuedRequest.request.method === 'POST') {
        return copy(queuedRequest.request.data);
      }
      if (queuedRequest.request.method === 'PUT') {
        let resultCopy = copy(result);
        resultCopy.data = queuedRequest.request.data.data;
        resultCopy.modified = new Date().toISOString();
        return resultCopy;
      }
      if (queuedRequest.request.method === 'DELETE') {
        return null;
      }
    }, submission))
    .catch((err) => {
      console.warn('Error retrieving offline submission:', err);
      throw err;
    });
  }

  /**
   * Deletes a submission from the offline data
   * @param  formId       _id or path of submission's form
   * @param  submissionId _id of the submission
   * @return              Promise of delete operation
   */
  _deleteOfflineSubmission(formId, submissionId) {
    let {_id, path} = this._resolveFormId(formId);
    let deleteError = null;
    const paths = [
      path + '-' + submissionId,
      _id + '-' + submissionId
    ];
    return paths.reduce((prev, idPath) => {
      return prev.then(() => {
        return this.db.removeItem(OFFLINE_SUBMISSION_PREFIX, idPath).catch((err) => {
          deleteError = err;
        });
      });
    }, Promise.resolve())
      .catch(() => {
        console.warn('Error deleting offline submission:', err);
        throw err;
      })
      .then(() => {
        if (deleteError) {
          console.warn('Error deleting offline submission:', deleteError);
          throw deleteError;
        }
      });
  }

  /**
   * A unique filter to be used with Array.prototype.filter
   */
  _uniqueFilter(value, index, self) {
    return self.indexOf(value) === index;
  }
}
