import Formio from 'formiojs/Formio';
import FormioOfflineProject from './index';
const scripts = document.getElementsByTagName('script');
let thisScript = null;
let i = scripts.length;
while (i--) {
  if (scripts[i].src && (scripts[i].src.indexOf('formio.offline') !== -1)) {
    thisScript = scripts[i];
    break;
  }
}

if (thisScript) {
    const query = {};
    let scriptSrc = thisScript.src.replace(/^([^?]+).*/, '$1').split('/');
    scriptSrc.pop();
    scriptSrc = scriptSrc.join('/');
    const queryString = thisScript.src.replace(/^[^?]+\??/,'');
    queryString.replace(/\?/g, '&').split('&').forEach((item) => {
        query[item.split('=')[0]] = item.split('=')[1] && decodeURIComponent(item.split('=')[1]);
    });

    if (query.project) {
        var plugin = new FormioOfflineProject(query.project);
        Formio.registerPlugin(plugin, 'formio-offline');
        const id = `formio-offline-${Math.random().toString(36).substring(7)}`;
        const className = query.class || 'formio-offline-widget';   
        if (query.goto) {
            document.write(`<a id="${id}" class="${className}" href="${query.goto}"></a>`);
        } 
        else {
            document.write(`<span id="${id}" class="${className}"></span>`);
        }
        const offlineElement = document.getElementById(id);
        plugin.statusIndicator(offlineElement, !!query.goto);
    }
}